import React from 'react';
import ReactDOM from 'react-dom'
import {Container,Jumbotron} from 'react-bootstrap'
import "../styles/welcomeInfo.css"

const Manual = () => {
    return(
        <Container id="info">
            <Jumbotron>
                <h1>Zasady gry</h1>
                <br/>
                <h3>Kategorie zawodnikw</h3>
                    
                <p>
                Aby utworzy druyn gracz musi wybra 10 zawodnikw. Gracz jest zobowizany do
                wyboru dokadnie 3 juniorw oraz maksymalnie 4 obcokrajowcw. Oznacza to e
                druyna moe si skada z :
                <ul>
                    <li>3 juniorw, 3 seniorw i 4 obcokrajowcw</li>
                    <li>3 juniorw, 4 seniorw i 3 obcokrajowcw</li>
                    <li>3 juniorw, 5 seniorw i 2 obcokrajowcw</li>
                    <li>3 juniorw, 6 seniorw i 1 obcokrajowca</li>
                    <li>3 juniorw, 7 seniorw i 0 obcokrajowcw</li>
                </ul>
                Druyna skada si z 7 zawodnikw pierwszego skadu oraz 3 zawodnikw
                rezerwowych. W pierwszym skadzie musi si znale dokadnie 2 juniorw i
                maksymalnie 3 obcokrajowcw.
                Kady gracz moe posiada tylko jedn druyn.
                </p>
                <hr className="my-4" />
                 <h3>Budet</h3>
                <p>
                Kady z graczy ma do wykorzystania 10 milionw wirtualnej waluty. Przy zakupie
                danego zawodnika do druyny, budet jest zmniejszany o warto zawodnika. Ceny
                zawodnikw s ustalane na podstawie ich wynikw z poprzednich sezonw.
                Jeli gracz sprzeda zawodnika, otrzymuje z powrotem pienidze, ktre moe
                wykorzysta na zakup innego zawodnika.
                </p>
                <hr className="my-4" />
                 <h3>Punktacja</h3>
                <p>
                Punkty s przyznawane za zawodnikw znajdujcych si w pierwszym skadzie. Ilo
                punktw w grze odpowiada realnym wynikom zawodnikw w meczach i jest rwna
                sumie punktw biegowych oraz bonusw. Dla przykadu: Piotr Pawlicki zdobywa
                11pkt i 2 bonusy w meczu ze Spart Wrocaw. Oznacza to e zdobywa 13 pkt w grze
                za t kolejk spotka, w ktrej rozgrywany by mecz. 
                </p>
                <br/>
                <p>Wszystkie punkty zdobyte przez zawodnikw pierwszego skadu w danej kolejce s
                sumowane do punktacji druyny gracza. </p>
                <hr className="my-4" />
                 <h3>Kapitan</h3>
                <p>
                Kady gracz wybiera kapitana i vice kapitana swojej druyny. W kadej kolejce punkty
                ktre zdoby kapitan s liczone razy dwa. Jeli kapitan nie pojecha w danej kolejce
                podwjne punkty zdobywa vice kapitan. Jeli on rwnie nie pojecha, aden
                zawodnik nie dostaje podwjnych punktw.
                </p>
                <hr className="my-4" />
                 <h3>Rezerwowi</h3>
                <p>
                Jeli w danej kolejce ktry z zawodnikw pierwszego skadu nie pojedzie w meczu,
                na jego miejsce automatycznie wchodzi rezerwowy (jeli rezerwowy pojecha).
                Zasady rezerwowych:
                <ul>
                    <li>za juniora moe wej tylko junior</li>
                    <li>za obcokrajowca lub seniora moe wej obcokrajowiec lub senior, z
                        zachowaniem zasady e w pierwszym skadzie moe by maksymalnie 3
                        obcokrajowcw</li>
                    <li>rezerwowi obcokrajowcy i seniorzy wchodz w kolejnoci takiej jakiej s na
                        awce rezerwowych (najpierw wchodzi rezerwowy oznaczony numerem 1,
                        potem numerem 2, numer 3 jest zarezerwowany dla juniora).</li>
                </ul>

                </p>
                
                </Jumbotron>
        </Container>
    )
}

export default Manual;