import React from 'react';
import ReactDOM from 'react-dom';
import {Container,Jumbotron} from 'react-bootstrap'
import "../styles/welcomeInfo.css"

const WelcomeInfo = () => {
    return(
        <Container id="info">
            <Jumbotron>
                <h1>Docz do Fantasy Ekstraliga ju dzisiaj !</h1>
                <p>
                    Tylko chwila dzieli ci od stworzenia wasnej druyny ulowej. 
                    <br/>
                    Dziki Fantasy Ekstraliga bdziesz 
                    mg/moga poczu si jak prawdziwy menader ulowy.
                </p>
                    <hr className="my-4" />
                <p>
                    Podczas gry bdziesz :
                    <ul>
                        <li>Dokonywa transferw</li>
                        <li>Rozsdnie dysponowa budetem</li>
                        <li>Rywalizowa ze znajomymi</li>
                        <li>Obserwowa poczynania twoich zawodnikw na stadionach w caej Polsce</li>
                    </ul>
                </p>
 

                </Jumbotron>
        </Container>
    )
}
  


export default WelcomeInfo;